/*--------------------------------------------------------------
# Page Templates
--------------------------------------------------------------*/

/* Front page */

.blog .content-area, .single-post .content-area, .date .content-area, .error404 .content-area, .search .content-area,.page-id-71 .content-area{
	width:70%;
	padding-right:3rem;
}
 .widget-area{
	border-left:solid 1px lightgrey;
	width:30%;
	padding:2.5rem;
}
.single .site-content, .error404 .site-content, .search .site-content{
	width:1120px;
	margin-left: auto;
	margin-right:auto;
}

	.blog .entry-title, .single-post .entry-title, .archive .entry-title{
		position:absolute;
		padding-left: 1rem;
		font-size:2rem;
		text-transform:uppercase;
    	top: 10px;
		background-color: #248a83;
		width: 30rem;
		color:white;
		padding: .6rem;
		padding-left: 1rem;
		a{
			color:white;
			text-decoration:none;
			&:visited{
				color:white;
			}
			&:hover{
				color:$color__background-ins;
			}
		}
	}
	//css positioning for entry meta
	.blog .entry-meta, .single-post .entry-meta, .archive .entry-meta{
		position:absolute;
		bottom: 6.5px;
		color: white;
		right: 0px;
		text-transform:uppercase;
		background-color: #248A83;
		padding: .3rem 1.5rem;
		color:white;
		a{
			text-decoration:none;
			&:hover{
				color:$color__background-ins;
			}
			&:visited{
				color:white;
			}
		}
		.comments-link a{
			&:hover{
				color:$color__background-ins;
			}
		}
	}


.about-hero{
	background: linear-gradient( to bottom, rgba(0,0,0,0.4) 0%, rgba(0,0,0,0.4) 100% ),
	url(../../images/about-hero.jpg) no-repeat center bottom;
	background-size:cover,cover;
	height:800px;
	position:relative;
}

.home .shop-container{
	display:inline-flex;
	justify-content: center;
	width:1120px;
	.shop-stuff-icons{
		flex: 0 1 23%;
		max-width:23%;
		border:solid 1px lightgrey;
		border-top:solid 5px lightgrey;
		padding:.75rem;
		padding-bottom:1.5rem;
		text-align: center;
		margin:.7rem;
		align-items:baseline;
		&:hover{
		border:solid 1px #d99054;
		border-top:solid 5px #d99054;
		}
		a{
			color:white;	
			text-decoration:none;	
			background:#248A83;
			padding: .75rem 1.5rem;	
			font-size: .9rem;
			text-transform:uppercase;
			&:hover{
			background:#1b6661;
		}
		}
		
		img{
			width:70px;
			height:70px;
		}
	}
}
.home h2{
	text-align:center;
	text-transform: uppercase;
	font-size:2.4rem;
}

//journal css stuff
.journal-entry{
	max-width:100%;
	display:flex;
	justify-content:center;
	.a-post{
		border: solid 1px lightgrey;
		margin: .75rem;
		flex: 0 1 32%;
		
		.a-post-image{
			border-bottom:solid 5px lightgrey;
			height:250px;
			img{
			width:100%;
			height:246px;
		}
		}
		.a-post-info{
			padding:1rem;
			p{
				margin-top:.3rem;
			}
		}
	}

}

.home .journalExcerpt a{
	color: black;
    background: white;
    border: solid 1px black;
    text-decoration: none;
    padding: .6rem 1.3rem;
    text-transform: uppercase;
    margin-bottom: 1.1rem;
	&:hover{
		background:#248a83;
		color:white;
		border: solid 1px #248a83;

	}
}	
.journal-3{
	display:inline-flex;
	justify-content: space-between;
	width:1120px;
}
.home .entry-header .entry-title{
	display:none;
}
.category {
	text-transform:uppercase;
	border-bottom: 1px dashed #c8c8c8;
	padding-bottom: 2.5rem;
	text-align: center;
	margin-bottom: 2rem;
	width:1120px;
	a{
		padding: 0rem 1.3rem;
		text-decoration:none;
	}
}
.post-type-archive .page-title{
	text-transform:uppercase;
		letter-spacing: 2px;
}
//journal readmore buttons
.blog .entry-content, .archive .entry-content{
	a{
		border: solid 1px black;
		padding: .60rem 1.4rem;
		text-decoration:none;
		text-transform:uppercase;
		font-size:.8rem;
		color:black;

		&:hover{
			color:white;
			background:#248A83;
			border:solid 1px #248A83;
		}
	}
}
// journal images
.post .entry-header{
	position:relative;
	margin-top:2.5rem;
	img{
		width:760px;
		height:520px;
	}
}
//find us page
.page-id-71 .site-content{
	max-width:1120px;
	margin: auto;
}

//taxonomy products categories

.tax-product_type .page-header{
	.page-title{
		text-transform:uppercase;
	}
	border-bottom: 1px dashed #c8c8c8;
	padding-bottom: 1rem;
	text-align: center;
	margin-bottom: 2.5rem;
	width:1120px;
}
//single page entry-footer css
.single .entry-footer{
	.cat-links{
		margin-right: 1.5rem;	
	}
	a{
		text-decoration: none;
	}
}
//shop category css
.archive-info{
	display:flex;
	justify-content:space-between;
	padding:.7rem 1rem;
	border-top:solid 5px lightgrey;
	border:solid 1px lightgrey;
	margin-bottom: 2rem;
	position:relative;
	.dotStuff{
		position:absolute;
	    top: -4.5px;
		right: 68px;
		letter-spacing: -1px;
		z-index: -1;
	}
	.productTitle{
		background:white;
		padding-right:.2rem;
	}
	.priceTag{
		background:white;
		padding-left: .2rem;
	}
}
.archive-product{
	height:250px;
	border-bottom:solid 5px lightgrey;
	z-index:100;
	img{
		height:100%;
		object-fit: cover;
	}
}
// journal archives page
.date .page-header{
	border-bottom:solid 1px lightgrey;
}

//single-page products
.singleContentWrapper{
	display:flex;
	padding-top:3rem;
	.entry-header, .entry-content{
		flex:1;
	}
	.entry-content{
		padding-right:3rem;
		.entry-title{
			margin-top:0;
		}
		.socialMediaButtons{
			display:inline-flex;
			i{
			width:20px;
			}
			a{
				padding:.6rem 1.25rem;
				margin:.1rem;
				border:solid 1px  black;
				&:hover{
					border:solid 1px  #248a83;
				}
			}
		}
	}
	.entry-header{
		margin-right: 2.5rem;
		margin-left:.5rem;
	}
}
.single-post .socialMediaButtons{
			display:inline-flex;
			i{
			width:20px;
			}
			a{
				padding:.6rem 1.25rem;
				margin:.1rem;
				margin-top:1rem;
				border:solid 1px  black;
				&:hover{
					border:solid 1px  #248a83;
				}
			}
		}


.single-post .entry-footer{
	padding-bottom:1rem;
}
// COMMENTS STUFF
.comments-title{
		font-size:2rem;
		letter-spacing: 2px;

}
.comment-body{
	display:inline-flex;
	padding-bottom:.5rem;
	.comment-content{
		padding-left:1rem;
		
	}
}
.children{
	// border-top: solid 1px lightgrey;
	padding-top:1rem;
	margin-left:4rem;
	margin-bottom:1rem;
}
.comment-list{
	.parent{
		padding-top:0;
		border-top:solid 0px lightgrey;
	}
}

.comment{
	border-top:solid 1px lightgrey;
	padding-top:1rem;
}
.comment-respond{
	border-top:solid 1px lightgrey;
}

//END OF COMMENT section
//STUFF
.textwidget{
	i{
		width:25px;
	}
}
//ABOUT PAGE STUFF 
.page-template-about{
	.entry-title{
		margin-top:0;
		height:100vh;
		width:100%;
		line-height:22;
		text-align: center;
		color:white;

	}
	.container{
		width:800px;
		h2{
			text-align: center;
		}
	}

}
//search page
.search{
	.page-title{
		border-bottom: solid 1px lightgrey;
		padding-bottom:1rem;
	}

}
